.card {
  border: 4px rgb(99, 99, 99) solid;
  border-radius: 2px;
  width: 200px;
  height: 200px;
  margin: 0.5rem;
  position: relative;
  box-shadow: 4px 4px 10px rgb(56, 56, 56);
  -webkit-transition: all 0.2s ease-in-out;
  transition: all 0.2s ease-in-out;
}

.card > .img-container {
  height: 100%;
  overflow: hidden;
  background: rgb(255, 255, 255);
}

.card > .img-container > img {
  height: 100%;
}

.card:hover {
  box-shadow: 8px 8px 20px rgb(56, 56, 56);
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
  cursor: pointer;
}

.card:active {
  -webkit-transform: translate(1px, 1px);
          transform: translate(1px, 1px);
}

.navbar {
  display: inline-block;
  text-align: center;
}

.itemLeft {
  width: 15%;
}

.itemCenter {
  width: 70%;
}

.itemRight {
  width: 15%;
}

.navbar {
  background: #212f3d;
  box-shadow: 0 3px 6px #999, 0 3px 6px #999;
}

body {
  font-size: 24px;
}

h3 {
  text-align: center;
  font-size: 20px;
  padding-bottom: 40px;
}
h5 {
  font-family: "Shrikhand", cursive;
  font-size: 1em;
  color: rgb(10, 87, 169);
}

.wrapper {
  background: whitesmoke;
  background-image: url("http://speedhunters-wp-production.s3.amazonaws.com/wp-content/uploads/2016/02/03054315/2016-ADRL-Round-Three-by-Paddy-McGrath-9-1200x800.jpg");
  height: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: row wrap;
          flex-flow: row wrap;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-align-content: flex-start;
          align-content: flex-start;
  overflow: auto;
  padding-bottom: 50px;
}

.title {
  width: 100%;
  text-align: center;
  font-size: 2rem;
  color: rgb(124, 3, 3);
  margin-bottom: 50px;
  padding-left: 3%;
  padding-right: 3%;
  font-family: "Righteous", cursive;
}

.wrapper {
  height: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: row wrap;
          flex-flow: row wrap;
  padding: 25px 0 50px;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-align-content: flex-start;
          align-content: flex-start;
  border-radius: 50px;
}

* {
  box-sizing: border-box;
  font-family: "Shrikhand", cursive;
}

html,
body,
#root {
  padding: 0;
  margin: 0;
  height: 100%;
}

